import React from 'react'
import styled from 'styled-components'
import birthdayData from '../data/BirthdayData'
import PropTypes from 'prop-types';
import sizeMe from 'react-sizeme';
import Confetti from 'react-confetti';

const Message = styled.div`
  white-space: nowrap;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 4rem;
  @media screen and (max-width: 900px) {
    white-space: default;
    font-size: 2rem;
  }
`

const Container = styled.div`
  text-align: center;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`
// eslint-disable-next-line
const Wisher = styled.div`
  display: block;
  white-space: nowrap;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 2rem;
  margin-top: 1rem;
  @media screen and (max-width: 900px) {
    font-size: 1.5rem;
    white-space: default;
  }
`

class BirthdayMessage extends React.Component {
  componentWillMount() {
    document.body.style.background = birthdayData.color
    document.querySelector('title').innerText = `Happy Birthday, ${birthdayData.wishee}!`
    document.querySelector('.favicon').href = `//dummyimage.com/64x64/${birthdayData.color.substring(1)}/${birthdayData.color.substring(1)}`
    document.querySelector('.theme-color').content = birthdayData.color
  }

  render() {
    const { size } = this.props;

    return (
      <div style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%' }}>
        <Container>
        <Message style={{ color: birthdayData.textColor }}>
          Happy Birthday {birthdayData.wishee}
        </Message>
        {/* <Wisher style={{ color: birthdayData.textColor }}>
          -&nbsp;{birthdayData.wisher}
        </Wisher> */}
        </Container>
        <Confetti {...size} />
      </div>
    );
  }
}

BirthdayMessage.propTypes = {
  size: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number,
  }),
};

export default sizeMe({
  monitorHeight: true,
  monitorWidth: true,
})(BirthdayMessage);